import { getUser, updateUser } from "@/api/user";

// 仅在客户端加载 js-cookie 包
const Cookie = process.client ? require("js-cookie") : undefined;

export default {
  middleware: "authenticated",
  name: "settingIndex",
  data() {
    return {
      disable: false,
    };
  },
  async asyncData() {
    const { data } = await getUser();
    const { user } = data;
    return {
      user,
    };
  },
  methods: {
    // 更新用户信息
    async updateSetting() {
      this.disable = true;
      await updateUser({
        user: {
          email: this.user.email,
          bio: this.user.bio,
          image: this.user.image,
        },
      });
      this.disable = false;
      // 跳转到用户界面
      this.$router.push({
        name: "profile",
        params: {
          username: this.user.username,
        },
      });
    },
    quit() {
      // 清除用户的登录状态
        this.$store.commit('setUser', null)
      Cookie.remove("user");
      // 跳转到首页
      this.$router.push("/");
    },
  },
};
